:root {
  --primary-color: rgb(144, 153, 161);
  --secondary-color: rgba(230, 230, 230, 0.95);
  --border-color: rgb(205, 213, 221);
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "Montserrat", Arial, sans-serif;
  text-rendering: optimizeLegibility;
  background: url(/portfolio-manager/static/media/background.c795ad8c.jpg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
}

/* NAVIGATION STYLES */

.menu-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.menu-item {
  margin: 2rem;
  font-size: 1.4rem;
  font-weight: bold;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  letter-spacing: 0.1rem;
  list-style: none;
  color: var(--primary-color);
}

.active-menu-item {
  color: black;
  border-bottom: 0.1rem solid black;
}

.menu-item:hover {
  color: black;
  cursor: pointer;
}

/* FOOTER STYLES */


/* PRICES MAIN PAGE STYLES */

.price-container {
