.price-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 0.1rem;
    border-bottom: 0.1rem solid var(--secondary-color);
