    font-size: 1.2rem;
}

.ticker-price {
    font-family: "Open Sans", Arial, sans-serif;
    font-size: 1.6rem;
    letter-spacing: 0.1rem;
    font-weight: 400;
}

.price-item:nth-of-type(odd) {
    color: red;
}

.price-item:nth-of-type(even) {
