    margin-bottom: 1rem;
}

.portfolio-totals {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 2rem;
    padding: 0 4rem;
    font-family: "Open Sans", Arial, sans-serif;
}

.portfolio-total-value {
    font-size: 1.8rem;
}

