/* PORTFOLIO ASSETS STYLES */

.portfolio-header {
    display: grid;
    grid-template-columns: 2fr 4fr 4fr;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    text-align: center;
}

.portfolio-assets {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

