@-webkit-keyframes open {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes open {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.settings-background {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 0;
  }

.settings-popup {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 20rem;
  width: 32rem;
  background-color: var(--border-color);
  border: 1px solid white;
  color: black;
  box-shadow: 3px 3px 6px rgba(0,0,0,0.8);
  top: 10%;
  left: 50%;
  margin-left: -16rem;
  -webkit-animation: open 0.5s;
          animation: open 0.5s;
}

.settings-popup-title {
  display: -webkit-flex;
  display: flex;
  font-size: 1.6rem;
  padding: 1.4rem 0 1.2rem 1.2rem;
